@import url(https://fonts.googleapis.com/css?family=Lora:400i|Montserrat:500,700|Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* FONTS, SIZE AND COLORS */
/* ---------------------------------------- */

.App {
  background-color: white;
  color: #242424;
  font-size: 1em;
  font-family: "Raleway", sans-serif;
}
.App p,
h2,
h3,
h4,
h5,
small,
hr,
span {
  margin: 5% 10% 0;
}

.App h1 {
  margin: 15% 20% 5% 10%;
  font-size: 1.5em;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
}

.App h2 {
  font-size: 1.3em;
  line-height: 1.3em;
  font-family: "Lora", serif;
}
.App h3 {
  font-size: 1.2em;
  line-height: 1.2em;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  /* text-transform: uppercase; */
  font-weight: 700;
}

.App h4 {
  font-size: 1em;
  font-family: "Lora", serif;
}

.App h5 {
  font-size: 1em;
  line-height: 1em;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  font-weight: 700;
}

.App a,
button {
  font-family: "Raleway", sans-serif;
  color: aliceblue;
  text-decoration: none;
  margin: 5%;
}

.App small {
  color: #242424;
  font-size: 0.8em;
  font-family: "Lora", serif;
  text-align: right;
}

footer {
  margin: 5vh 0 0 0;
  padding: 5%;
  text-align: center;
  background-color: black;
}

.date-day {
  color: aliceblue;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  font-weight: bolder;
  font-size: 1.4em;
  text-decoration: underline;
}
.date-month {
  color: aliceblue;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  font-weight: bolder;
  font-size: 1.4em;
}
.date-year {
  color: aliceblue;
  font-family: "Montserrat", "Helvetica Neue", sans-serif;
  font-weight: bolder;
  font-size: 1.4em;
  position: absolute;
  bottom: 10px;
}

/* SEPARATORS */

.App hr {
  border: 0.5px solid silver;
}

.App .small-hr {
  width: 50px;
  border: 2px solid blue;
  /* margin: 10px auto; */
}

/* SECTION OFF-GRID SETTINGS */

.App .off-grid-section {
  margin-bottom: -50px;
}

.App .Header {
  margin-bottom: 150px;
}

/* CARDS AND LIST */

.inline-card {
  padding: 12px;
  margin: 10% 20% 10% 0%;
  height: 375px;
  background-image: -webkit-linear-gradient(
      rgba(0, 40, 250, 0.4) 0%,
      rgba(0, 40, 250, 0.4) 80%
    ),
    url("https://picsum.photos/500/300/?random");
  background-image: linear-gradient(
      rgba(0, 40, 250, 0.4) 0%,
      rgba(0, 40, 250, 0.4) 80%
    ),
    url("https://picsum.photos/500/300/?random");
  border-radius: 5px;
  width: 250px;
  margin-left: 15px;
  position: relative;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
}
.card-text {
  position: absolute;
  bottom: 5%;
}

.inline-card p {
  top: 10px;
}

.inline-carousel {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  overflow-x: auto;
}

.outline-text {
  border: 1px solid aliceblue;
  color: aliceblue;
  padding: 5px 10px;
  border-radius: 15px;
  position: absolute;
  top: 5%;
}

.inline-thin-card {
  margin: 10% 15px 10% 0%;
  /* height: 200px; */
  border-radius: 5px;
  min-width: 300px;
  /* width: 100%; */
  margin-left: 15px;
  position: relative;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
  display: -webkit-flex;
  display: flex;
}
.card-thin-img {
  background-image: -webkit-linear-gradient(
      rgba(0, 40, 250, 0.4) 0%,
      rgba(0, 40, 250, 0.4) 80%
    ),
    url("https://picsum.photos/500/300/?random");
  background-image: linear-gradient(
      rgba(0, 40, 250, 0.4) 0%,
      rgba(0, 40, 250, 0.4) 80%
    ),
    url("https://picsum.photos/500/300/?random");
  background-position: cover;
  width: 30%;
  border-radius: 5px 0 0 5px;
  position: relative;
}

.text-thin-card h3 {
  color: #242424;
}

.column-card {
  background-image: -webkit-linear-gradient(
      rgba(0, 40, 250, 0.4) 0%,
      rgba(0, 40, 250, 0.4) 80%
    ),
    url("https://picsum.photos/500/300/?random");
  background-image: linear-gradient(
      rgba(0, 40, 250, 0.4) 0%,
      rgba(0, 40, 250, 0.4) 80%
    ),
    url("https://picsum.photos/500/300/?random");
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
  background-size: cover;
  border-radius: 5px;
  margin: 5%;
  padding: 2% 0;
  text-align: center;
  max-width: 400px;
  min-width: 200px;
}

/* SMALL CARDS FOR ATTENDING EVENTS */

.flex-small-cards {
  margin: 5%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  /* align-content: flex-start; */
}

/* .small-card-date {
  background-color: white;
  width: 100%;
} */

/* FOR IMAGED CARDS ONLY */

.column-card-with-image {
  background-color: white;
  border-radius: 5px;
  margin: 5% 5% 5% 20%;
  padding: 2% 0;
  color: #242424;
  position: relative;
  height: 120px;
}
.column-card-with-image img {
  width: 100px;
  margin-left: -50px;
  margin-top: 10px;
  border-radius: 5px;
}

.column-card-with-image .column-card-with-image-text {
  position: absolute;
  top: 5%;
  right: 3%;
  /* padding-left: 0px; */
  text-align: right;
}

/* DESKTOP */

@media screen and (min-width: 992px) {
  .App .desktop-cards-section {
    margin: 15% 0 7%;
    display: -webkit-flex;
    display: flex;
    /* align-items: center; */
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }

  .App .desktop-card-heading {
    max-width: 30%;
    background-color: white;
    padding: 10%;
  }

  .App .desktop-caroussel {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  .App .flex-item1 {
    -webkit-order: 2;
            order: 2;
    margin-top: -10%;
  }
  .App .flex-item2 {
    -webkit-order: 1;
            order: 1;
    margin-top: -10%;
  }
}

/* TESTS */

.HomePage div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  /* height: 30vh; */
}

.HomePage a,
.big-btn {
  padding: 20px 30px;
  border-radius: 5px;
  width: 80%;
  text-align: center;
}

.HomePage div a:first-child {
  background-color: #1cb954;
}

.HomePage div a:nth-child(2) {
  background-color: #32323d;
}

.HomePage div a:nth-child(3) {
  background-color: #f2f2f2;
  color: #3c3c3c;
}

.big-btn {
  background-color: tomato;
}

.TopArtists {
  padding-top: 15%;
}

.profilPic {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 3px solid white;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
  position: absolute;
}

.img-flex {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 20px;
}

